import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useEffect, useState} from 'react';
import { gsap } from 'gsap';


export default function Navigation() {
  const pathname = usePathname();
  const [activeLink, setActiveLink] = useState('');

  useEffect(() => {
    setActiveLink(pathname);
  }, [pathname]);

  useEffect(() => {
    gsap.from('.nav-link', {
      y: -20,
      opacity: 0,
      duration: 0.5,
      stagger: 0.1,
    });
  }, []);
  

  return (
    <div className="d-flex justify-end align-end p-4  bg-blur-[2px] backdrop-blur-md bg-white/10">
        <nav className="flex justify-end gap-4">
            <Link href="/" className={`nav-link ${activeLink === '/' ? 'active' : ''} text-xl`}>
                Home
            </Link>
            <Link href="/about" className={`nav-link ${activeLink === '/about' ? 'active' : ''} text-xl`}>
                About
            </Link>
            <Link href="/contact" className={`nav-link ${activeLink === '/contact' ? 'active' : ''} text-xl`}>
                Contact
            </Link>
        </nav>
    </div>
    
  );
}